import React from "react";
import { Grid, Image, Text, Button } from "../elements";
import theme from "../styles/theme";
import { history } from "../redux/configureStore";
import { IoIosCloseCircle } from "react-icons/io";
import { customAlert } from "./Sweet";
import logger from "../shared/Console";

const ChatUserItem = (props) => {
  const { fontSize, color, radius } = theme;
  const user_info = props.user_info;
  const shape = props.shape;
  const owner = props.owner;

  React.useEffect(() => {
    logger("sendBen ", props);
  }, []);

  if (shape === "is_me") {
    return (
      <Grid is_flex4="t" padding="0.8rem 0 0.8rem 2rem">
        <Image
        margin="0.4rem 0.8rem 0.4rem 0" 
        cursor="pointer"
        _onClick={() => {
          return history.push({
            pathname: "/myprofile",
            state: { ...user_info },
          })
        }}
        src={user_info.user_img}></Image>
        <Text bold2="700" size={fontSize.base}>
           ({user_info.user_name})
        </Text>
        {user_info.user_id === props.own_user_id && (
          <Text
            width="2.7rem"
            height="1.5rem"
            margin="0 0 0 0.4rem"
            size={fontSize.tiny}
            bg={color.brand20}
            color={color.brand100}
            radius="0.4rem"
            bold2="700"
            text_align="center"
          >
            
          </Text>
        )}
      </Grid>
    );
  }

  //      
  if (owner) {
    return (
      <Grid is_flex4="t" padding="0.8rem 2rem">
        <Grid is_flex4="t">
          <Image
            margin="0.4rem 0.8rem 0.4rem 0"
            cursor="pointer"
            shape="circle"
            src={user_info.user_img}
            _onClick={() => {
              history.push({
                pathname: "/userprofile",
                state: { ...user_info },
              });
            }}
          ></Image>
          <Text size={fontSize.base}>{user_info.user_name}</Text>
        </Grid>
        <IoIosCloseCircle
          size="2.4rem"
          color={color.bg60}
          onClick={() => {
            customAlert.SweetBen(
              props.sendBen,
              user_info.user_id,
              user_info.user_name
            );
          }}
        />
      </Grid>
    );
  }

  return (
    <Grid is_flex4="t" padding="0.8rem 0 0.8rem 2rem">
      <Image
        margin="0.4rem 0.8rem 0.4rem 0"
        cursor="pointer"
        shape="circle"
        src={user_info.user_img}
        _onClick={() => {
          history.push({
            pathname: "/userprofile",
            state: { ...user_info },
          });
        }}
      ></Image>
      <Text size={fontSize.base}>{user_info.user_name}</Text>
      {user_info.user_id === props.own_user_id && (
        <Text
          padding="0.1rem"
          width="2.7rem"
          height="1.5rem"
          margin="0 0 0 0.4rem"
          size={fontSize.tiny}
          bg={color.brand20}
          color={color.brand100}
          radius="0.4rem"
          bold2="700"
          text_align="center"
        >
          
        </Text>
      )}
    </Grid>
  );
};

export default ChatUserItem;
