import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { token } from "../shared/OAuth";
import { history } from "../redux/configureStore";
import moment from "moment";

//  
import Stomp from "stompjs";
import SockJS from "sockjs-client";

// style
import styled from "styled-components";
import Spinner from "../shared/Spinner";
import { Header, MessageList, MessageWrite, SideContent } from "../components";
import { Grid } from "../elements";
import { actionCreators as chatActions } from "../redux/modules/chat";
import { actionCreators as userAction } from "../redux/modules/user";
import theme from "../styles/theme";
import logger from "../shared/Console";
import { customAlert } from "../components/Sweet";
import "../styles/side.css";

// side bar
import Sidebar from "react-sidebar";

const Chat = (props) => {
  const { border } = theme;

  // side nav
  const [isOpen, setIsOpen] = React.useState(false);
  const onClick = () => {
    setIsOpen(!isOpen);
  };

  // 
  const sock = new SockJS("http://52.78.204.238/chatting");
  // const sock = new SockJS("http://115.85.182.57/chatting");
  const ws = Stomp.over(sock);
  // ws.reconnect_delay = 500;

  //  
  const dispatch = useDispatch();

  const roomName = props.history.location.state?.roomName;
  const room_id = props.history.location.state?.room_id;
  const post_id = props.history.location.state?.post_id;
  const own_user_id = props.history.location.state?.own_user_id;
  const order_time = props.history.location.state?.order_time;

  //     
  const user_in_chat = useSelector((state) => state.chat?.userInList);

  //   
  const sender_nick = useSelector((state) => state.user.user?.user_nickname);
  const sender_profile = useSelector((state) => state.user.user?.user_profile);
  const sender_id = useSelector((state) => state.user.user?.user_id);
  const messageText = useSelector((state) => state.chat.messageText);

  //      
  React.useEffect(() => {
    logger("chat props", props);
    logger("chat sender info", sender_profile);
    logger("chat user_in_chat", user_in_chat);
    dispatch(userAction.loginCheck());

    if (token) {
      dispatch(
        chatActions.moveChatRoom(
          room_id,
          roomName,
          post_id,
          own_user_id,
          order_time
        )
      );
      dispatch(chatActions.getChatMessagesAX());
      dispatch(chatActions.getChatUserAX(room_id));
    }
  }, []);

  //      , 
  React.useEffect(() => {
    if (!room_id) {
      return customAlert
        .sweetOK(
          " .",
          " .",
          "    .",
          ""
        )
        .then((res) => {
          return history.replace("/home");
        });
    }
    wsConnectSubscribe();
    return () => {
      wsDisConnectUnsubscribe();
    };
  }, [room_id ? room_id : null]);

  // ,    room_id   
  const wsConnectSubscribe = () => {
    try {
      // ws.debug = null;
      ws.connect(
        {
          token: token,
        },
        () => {
          ws.subscribe(
            `/sub/api/chat/rooms/${room_id}`,
            (data) => {
              const newMessage = JSON.parse(data.body);
              logger("   data", newMessage);

              //     
              const now_time = moment().format("YYYY-MM-DD HH:mm:ss");
              dispatch(
                chatActions.getMessages(
                  { ...newMessage, createdAt: now_time }
                )
              );
            },
            {
              token: token,
            }
          );
        }
      );
    } catch (e) {
      logger("  ", e);
    }
  };

  //         
  const wsDisConnectUnsubscribe = () => {
    try {
      // ws.debug = null;
      ws.disconnect(
        () => {
          ws.unsubscribe("sub-0");
          clearTimeout(waitForConnection);
        },
        { token: token }
      );
    } catch (e) {
      logger("   ", e);
    }
  };

  //      
  const waitForConnection = (ws, callback) => {
    setTimeout(() => {
      if (ws.ws.readyState === 1) {
        callback();
      } else {
        waitForConnection(ws, callback);
        
      }
    }, 0.001);
  };

  const sendMessage = (new_message) => {
    try {
      //    
      if (!token) {
        customAlert.sweetNeedLogin("replace");
      }
      // send 
      const data = {
        type: "TALK",
        roomId: room_id,
        sender: sender_nick,
        senderImg: sender_profile,
        senderId: sender_id,
        message: new_message,
        // message :   id
        // roomId :  
      };
      //    
      if (new_message === "") {
        customAlert.sweetConfirmReload(" .", null, "");
        return;
      }
      // 
      // dispatch(chatActions.loading());
      dispatch(chatActions.setTime());
      waitForConnection(ws, () => {
        // ws.debug = null;

        ws.send("/pub/message", { token: token }, JSON.stringify(data));
        logger(" ", ws.ws.readyState);

        //      
        dispatch(chatActions.writeMessage(""));
      });
    } catch (e) {
      logger("message   ", e);
      logger(" ", ws.ws.readyState);
    }
  };

  const sendBen = (other_user_id, other_user_name) => {
    try {
      //    
      if (!token) {
        customAlert.sweetNeedLogin("replace");
      }
      // send 
      const data = {
        type: "BAN",
        roomId: room_id,
        senderId: sender_id,
        //   user_id
        message: other_user_id,
      };

      // 
      // dispatch(chatActions.loading());
      waitForConnection(ws, () => {
        // ws.debug = null;

        ws.send("/pub/message", { token: token }, JSON.stringify(data));
        logger("  ", ws.ws.readyState);
        customAlert.sweetConfirmReload(
          '  ',
          [`${other_user_name}  .`],
          ''
        );
      });
    } catch (e) {
      customAlert.sweetConfirmReload(' ', ['   '], '');
      logger('message   ', e);
      logger('  ', ws.ws.readyState);
    }
  };

  //   
  const sendBreak = () => {
    try {
      //    
      if (!token) {
        customAlert.sweetNeedLogin("replace");
      }
      // send 
      const data = {
        type: "BREAK",
        roomId: room_id,
        senderId: sender_id,
        message: "  .  :)",
      };

      // 
      // dispatch(chatActions.loading());
      waitForConnection(ws, () => {
        // ws.debug = null;

        ws.send("/pub/message", { token: token }, JSON.stringify(data));
        logger("  ", ws.ws.readyState);
      });
    } catch (e) {
      customAlert.sweetConfirmReload(' ', ['   '], '');
      logger('message   ', e);
      logger('  ', ws.ws.readyState);
    }
  };

  if (!room_id) {
    return (
      // alert(" ")
      <React.Fragment>
        <Spinner />
      </React.Fragment>
    );
  } else {
    return (
      <React.Fragment>
        <Grid
          maxWidth="36rem"
          minHeight="100vh"
          border={border.line1}
          margin="0 auto"
          bg="#7B6E62"
        >
          <Grid shape="container" align_items="flex-end">
            <SideGrid isOpen={isOpen}>
              <Sidebar
                transitions={true}
                touch={true}
                pullRight={true}
                sidebar={
                  <SideContent
                    own_user_id={own_user_id}
                    order_time={order_time}
                    roomName={roomName}
                    _onClick={onClick}
                    post_id={post_id}
                    sendBen={sendBen}
                    sendBreak={sendBreak}
                  />
                }
                open={isOpen}
                onSetOpen={setIsOpen}
                sidebarClassName={isOpen ? "side-nav active" : "side-nav"}
                styles={{
                  content: { text_align: "right" },
                }}
              >
                <></>
              </Sidebar>
            </SideGrid>
            <Header
              {...props}
              shape=""
              roomName={roomName}
              _onClick={onClick}
            >
              {roomName}
            </Header>

            <MessageList />
            <MessageWrite sendMessage={sendMessage} />
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }
};

const SideGrid = styled.div`
  position: absolute;
  width: 30rem;
  height: 100vh;
  z-index: 101;
  text-align: right;
  display: ${(props) => (props.isOpen ? "auto" : "none")};
`;

export default Chat;
