const SweetChatRequest = (user_id, post_user_id, post_id) => {
  if (user_id === post_user_id) {
    sweet
      .fire({
        customClass: {
          popup: "border",
          confirmButton: "confirmButton",
          cancelButton: "cancelButton",
          denyButton: "denyButton",
          actions: "meal-action-class",
        },
        width: "auto",
        padding: "0 1rem 1rem",
        title: (
          <Text margin="0 auto 1rem" size={fontSize.base} bold2="700">
              .
          </Text>
        ),
        text: "  ?",
        showDenyButton: true,
        denyButtonText: (
          <Grid width="9rem" is_flex2 margin="auto">
            <Text padding="0" color={color.brand100} bold2={fontSize.bold}>
              
            </Text>
          </Grid>
        ),
        denyButtonColor: color.brand20,
        confirmButtonColor: color.brand100,
        confirmButtonText: (
          <Grid width="9rem" is_flex2 margin="auto">
            <Text padding="0" color={color.bg0} bold2={fontSize.bold}>
              
            </Text>
          </Grid>
        ),
        focusConfirm: false,
        reverseButtons: true,
      })
      .then((res) => {
        if (res.isConfirmed) {
          history.push("/chatlist");
        } else if (res.isDenied) {
          return;
        } else {
          return;
        }
      });
  } else {
    sweet
      .fire({
        customClass: {
          popup: "border",
          confirmButton: "confirmButton",
          cancelButton: "cancelButton",
          denyButton: "denyButton",
          actions: "meal-action-class",
        },
        width: "auto",
        padding: "0 1rem 1rem",
        title: (
          <Grid>
            <Text margin="0 auto 0" size={fontSize.base} bold2="700">
               ?
            </Text>
          </Grid>
        ),
        html: (
          <Grid>
            <Text size={fontSize.small} color={color.bg100} line_height="150%">
               , 
              <br />
                !
            </Text>
          </Grid>
        ),
        showDenyButton: true,
        denyButtonText: (
          <Grid width="9rem" is_flex2 margin="auto">
            <Text padding="0" color={color.brand100} bold2={fontSize.bold}>
              
            </Text>
          </Grid>
        ),
        denyButtonColor: color.brand20,
        confirmButtonColor: color.brand100,
        confirmButtonText: (
          <Grid width="9rem" is_flex2 margin="auto">
            <Text padding="0" color={color.bg0} bold2={fontSize.bold}>
              
            </Text>
          </Grid>
        ),
        focusConfirm: false,
        reverseButtons: true,
      })
      .then((res) => {
        if (res.isConfirmed) {
          axiosModule
            .get(`/posts/join/request/${post_id}`)
            .then((res) => {
              logger(" ", res);
              if (res.data === "  ") {
                sweetConfirmReload(
                  "  .",
                  ["   ."],
                  ""
                );
              } else {
                sweetConfirmReload(
                  "   ",
                  ["    ", "   ."],
                  ""
                );
              }
            })
            .catch((e) => {
              logger("    ", e);
            });
        } else if (res.isDenied) {
          sweetConfirmReload(" ", ["  ."], "");
        } else {
          return;
        }
      });
  }
};

