import React from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Grid } from '../elements';
import { Header, DetailPost } from '../components';
import { actionCreators as postActions } from '../redux/modules/post';

import theme from '../styles/theme';

//  console.log() 
import logger from '../shared/Console';

const DetailPage = (props) => {
  const dispatch = useDispatch();
  const id = props.match.params.id;
  const post_list = useSelector((state) => state.post.list);
  const user_info = useSelector((state) => state.user.user);
  const post_idx = post_list.findIndex((p) => p.post_id === parseInt(id));
  const post = post_list[post_idx];

  const chat_user_list = useSelector((state) => state.post.chat_user_list);

  logger(' -', chat_user_list);
  const is_me = post?.user_id === user_info?.user_id ? true : false;
  const { border } = theme;

  React.useEffect(() => {
    logger("", post_list)
    if (post) {
      return;
    }
    dispatch(postActions.getPostAX(''));
    dispatch(postActions.getDetailPostUserListAX(parseInt(id)));
  }, []);

  return (
    <React.Fragment>
      <Grid
        minHeight="100vh"
        maxWidth="36rem"
        border={border.line1}
        margin="0 auto"
      >
        <Grid shape="container">
          <Header {...props} shape="">
            {post?.title}
          </Header>
          <Grid height="4.4rem" />
        </Grid>

        {post && (
          <DetailPost {...post} chat_user_list={chat_user_list} is_me={is_me} />
        )}
      </Grid>
    </React.Fragment>
  );
};

export default DetailPage;
